.portfolio-tabs-content {
	position: relative;
	height: 100%;

	&__item {
		@include absoluteCover;

		display: block !important;
		opacity: 0;
		visibility: hidden;
		transition: opacity 0.3s, visibility 0.3s;

		&.active {
			opacity: 1;
			visibility: visible;
		}
	}

	&__item-content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		height: 100%;

		&.with-indent {
			padding: $portfolio-indent;
		}
	}

	&__information-block {
		max-width: 450px;
	}

	&__copyright {
		position: absolute;
		bottom: $portfolio-indent;
		left: $portfolio-indent;
		margin-bottom: 0;
		font-size: 5px;
	}
}

