// colors
$white:                      #ffffff !default;
$black:                      #000000 !default;
$gray:                       #DDDDDD !default;

// base
$body-bg:                    $white !default;
$body-font-size:             9px !default;
$body-line-height:           1.4 !default;
$body-letter-spacing:        0.1em !default;
$body-font-family:           "FTCalhern", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", "Noto Sans", "Liberation Sans", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !default;
$body-font-weight:           400;
$body-color:                 $black !default;
$body-text-transform:        uppercase !default;

$link-color:                 $black !default;

$portfolio-indent:            20px !default;

// z-index master list
$zindex-page-navigation:     1010 !default;
$zindex-lightbox-text-slide: 1070 !default;
$zindex-lightbox:            1070 !default;
$zindex-lightbox-initial:    1071 !default;
$zindex-lightbox-menu-btn:   1072 !default;
$zindex-lightbox-index-btn:  1072 !default;
$zindex-lightbox-header:     1075 !default;
$zindex-index-block:         1090 !default;
$zindex-preloader:           1099 !default;

// breakpoints
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px
) !default;