.portfolio-navigation {
	@include listReset;

	&__item {
		&.next-group {
			padding-top: 20px;
		}
	}

	&__btn {
		background-color: transparent;
		border: none;
		padding: 0;
		letter-spacing: inherit;
		cursor: pointer;
		color: inherit;
		transition: color 0.3s;

		&:not(.active) {
			&:hover {
				text-decoration: underline;
			}
		}

		&.active {
			color: $gray;
		}

		&.featured-btn {
			&.active {
				color: $white;
			}
		}
	}
}