.carousel-frame {
	@include customScrollbar(0, transparent, transparent);

	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	overflow-x: auto;
	height: 100%;

	&__list {
		@include listReset;
		@include customScrollbar(0, transparent, transparent);

		position: absolute;
		height: 100%;
		padding: 100px 0;
		display: flex;
		align-items: center;
		flex-wrap: nowrap;
		line-height: 1;
	}

	&__side-indent {
		flex-shrink: 0;
		padding-left: 30px;
		height: 20px;

		@include min-w(sm) {
			padding-left: 50px;
		}

		@include min-w(lg) {
			padding-left: 100px;
		}
	}

	&__link {
		display: inline-flex;
		align-items: flex-end;
		margin: 0 15px;
		flex-shrink: 0;
		opacity: 0;

		@include min-w(sm) {
			align-items: center;
			margin: 0 25px;
		}

		@include min-w(lg) {
			margin: 0 50px;
		}

		&:first-of-type {
			margin-left: 0;
		}

		&:last-of-type {
			margin-right: 0;
		}
	}

	&__img {
		height: 140px;
		cursor: pointer;
	}
}