.header-for-lightbox {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	width: 100%;
	z-index: $zindex-lightbox-header;
	opacity: 0;
	display: none;

	&__menu-btn {
		position: absolute;
		top: $portfolio-indent;
		left: $portfolio-indent;
		background-color: transparent;
		padding: 0;
		border: none;
		border-radius: 0;
		line-height: inherit;
		color: $link-color;
		cursor: pointer;

		&:hover {
			text-decoration: underline;
		}
	}

	&__caption {
		position: absolute;
		top: $portfolio-indent;
		left: 50%;
		transform: translate(-50%, 0);
		background-color: transparent;
		padding: 0;
		border: none;
		border-radius: 0;
		line-height: inherit;
		color: $link-color;
		cursor: pointer;

		&:hover {
			text-decoration: underline;
		}

		&.disabled {
			pointer-events: none;
		}
	}

	&__index-btn {
		@include sizeElement(20px);
		@include inlineFlexCenter;

		position: absolute;
		top: $portfolio-indent;
		right: $portfolio-indent;
		background-color: transparent;
		padding: 0;
		border: none;
		border-radius: 0;
		line-height: inherit;
		cursor: pointer;
		margin-top: -4px;
		transition: transform 0.3s;

		&:hover {
			transform: scale(1.2);
		}
	}
}