.fancybox-disable-transform-transition {
	.fancybox-container {
		transition: none !important;
	}
}

.fancybox {
	&-container {
		z-index: $zindex-lightbox-initial;

		&.for-zoom-animation {
			z-index: $zindex-lightbox;
		}

		&:not(.fancybox-is-closing) {
			.fancybox-slide {
				&.fancybox-animated {
					opacity: 1;
				}
			}
		}
	}

	&-is-open {
		.fancybox-bg {
			opacity: 0;
		}
	}

	&-slide {
		padding: 100px 0 !important;

		@include min-h(800) {
			padding: 100px 0 !important;
		}

		&--html {
			.fancybox-content {
				opacity: 0;
			}
		}
	}

	&-caption {
		display: none;
	}

	&-navigation {
		.fancybox-button {
			top: 0;
			bottom: 0;
			width: 50vw;
			height: 100vh;
			padding: 0;
			opacity: 0 !important;
			cursor: default;

			&--arrow_left {
				left: 0;
			}

			&--arrow_right {
				right: 0;
			}
		}
	}

	&-toolbar {
		position: static;

		.fancybox-button {
			&--go-to-next-category {
				@include absoluteCover;

				font-size: 0;
				padding: 0;
				background: transparent;
				z-index: 99997;
				cursor: default;
			}
		}
	}

	&-content {
		background-color: transparent;
		padding: 0;
		text-align: center;

		.fancybox-button {
			&.fancybox-close-small {
				display: none;
			}
		}
	}
}