.index-block {
	@include customScrollbar(0, transparent, transparent);

	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100vw;
	height: 100vh;
	height: 100svh;
	overflow: auto;
	z-index: $zindex-index-block;
	padding: 35px $portfolio-indent;
	transform: translate(0, 100%);
	pointer-events: none;

	@include min-w(sm) {
		padding: 35px;
	}

	&.open {
		pointer-events: inherit;
	}

	&__close-btn {
		position: absolute;
		top: 20px;
		left: 20px;
		z-index: 5;
		padding: 0;
		border: none;
		background-color: transparent;
		letter-spacing: inherit;
		color: $white;
		cursor: pointer;
		opacity: 0;

		&:hover {
			text-decoration: underline;
		}
	}

	&__category-name-badge {
		position: absolute;
		top: $portfolio-indent;
		left: 50%;
		transform: translate(-50%, 0);
		color: $white;
		opacity: 0;
		transition: opacity 0.5s;

		&.show {
			opacity: 1;
		}
	}

	&__list {
		@include listReset;

		display: flex;
		flex-wrap: wrap;

		&.hover-active {
			.index-block__list-link {
				opacity: 0.5;
			}
		}
	}

	&__list-link {
		display: inline-flex;
		padding: 10px;
		transition: opacity 0.3s;

		@include min-w(sm) {
			padding: 15px;
		}

		&:hover {
			opacity: 1 !important;
		}

		img {
			height: 75px;

			@include min-w(sm) {
				height: 100px;
			}
		}
	}
}