.portfolio-content {
	height: 100vh;
	height: 100svh;
	overflow: auto;

	&__preloader {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		z-index: $zindex-preloader;
	}

	&__preloader-text {
		@include absoluteCover;
		@include flexCenter;

		padding: $portfolio-indent;
		background-color: $gray;

		&.second {
			display: none;
			opacity: 0;
			z-index: 5;
		}
	}

	&__tabs-block {
		height: 100%;

		&.disable {
			pointer-events: none;
		}
	}

	&__author-badge {
		position: absolute;
		top: $portfolio-indent;
		right: $portfolio-indent;
		margin-bottom: 0;
		z-index: $zindex-page-navigation;

		@include min-w(sm) {
			right: auto;
			left: 50%;
			transform: translate(-50%, 0);
		}
	}

	&__navigation {
		position: absolute;
		top: $portfolio-indent;
		left: $portfolio-indent;
		z-index: $zindex-page-navigation;
	}
}